
.box
{
    width: 100%;

    height: 100%;
    background-color: #c7eae4;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
}
.btn
{
    width: 50%;
    height: 50px;
    background-color: #628395;
    border-radius: 10px;
    border: none;
    outline: none;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    margin: 10px 0;
    box-sizing: border-box;
}
.btn:hover
{
    background-color: #4e6773;
}
.btn:active
{
    background-color: #4e6773;
}
.otp
{
    width: 100%;
    height: 50px;
    background-color: #c7eae4;
    border-radius: 10px;
    letter-spacing: 10px;
    border: none;
    outline: none;
    font-size: 30px;
    font-weight: 500;
    cursor: pointer;
    margin: 10px 0;
    box-sizing: border-box;
    text-align: center;
    padding: 10px;
}